/** @format */

section.LinkedIn {
  padding-top: 20px;
  font-family: Roboto, "sans-serif";
  height: 100vh;
}

.link_social {
  margin: 0px 0px 10px 10%;
  opacity: 0.7;
  font-size: 15px;
  color: #2e3239;
}

.Icons {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Link_Progress {
  position: relative;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 10%;
}

.Link_Progress .Line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 58%;
  height: 3px;
  background: #12b0ee;
}

.Skip_Button {
  align-self: center;
}

.Skip_Button a {
  text-decoration: underline;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 13px;
  color: #24272c;
}

.Icon_Grid .Icon {
  width: 40px;
  border-radius: 3px;
  z-index: 1;
}

.Link_Progress .Icon {
  mix-blend-mode: luminosity;
  opacity: 0.5;
}

.Icon_Grid {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Icon_Grid .Icon:nth-child(1),
.Icon_Grid .Icon:nth-child(2),
.Icon_Grid .Icon:nth-child(3),
.Icon_Grid .Icon:nth-child(4) {
  mix-blend-mode: unset !important;
  opacity: 1 !important;
}

.Icon_Grid .Icon:nth-child(4) {
  border: 4px solid #12b0ee;
  border-radius: 10px;
  padding: 1px;
  box-shadow: 0 -1px 5px 0 rgba(0, 0, 0, 0.15);
}

.Form_Header {
  padding-top: 42px;
  display: grid;
  grid-template-columns: 15% 80%;
  margin-bottom: 40px;
}

.Form_Header .Back_Btn {
  margin-top: 5px;
  align-self: baseline;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

.Header_Text p:nth-child(2) {
  margin-top: 10px;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 14px;
  line-height: 1.43;
  color: #24272c;
}

.Header_Text p:nth-child(3) {
  font-size: 14px;
  font-weight: 500;
  color: #656f7a;
  margin-top: 8px;
  text-decoration: underline;
}

.Main {
  margin: 10px 0 0 15%;
}

.LinkedIn_Btn button {
  padding: 10px;
  align-items: center;
  display: flex;
  border-radius: 4px;
  background-color: #0078b7;
  font-size: 16px;
  margin-left: 15%;
}

.LinkedIn_Btn button img {
  border-radius: 5px;
  width: 35px;
  margin-right: 15px;
}

.hr_line {
  margin: -15px auto;
  opacity: 0.12;
  background-color: #24272c;
  width: 65%;
  height: 1px;
  background-color: #000;
}

.UserConfirmation {
  margin-top: 50px;
  margin-left: 15%;
  grid-template-columns: 10% 90%;
  margin-bottom: 10px;
}

.UserConfirmation label {
  font-size: 15px;
  color: #2e3239;
}

.UserConfirmation label a {
  color: black;
  text-decoration: underline;
}

.UserConfirmation input {
  margin-right: 10px;
  height: 20px;
  width: 20px;
}

.LinkedIn_FinishBtn {
  display: flex;
  align-items: center;
  border-radius: 6px;
  padding: 12px;
  margin-left: 15%;
}

.LinkedIn_FinishBtn img {
  width: 30px;
  margin-right: 6px;
}
