/** @format */

.Type0_Account {
  margin-top: 25px;
}

.Type0_Note {
  background-color: #f6f9fb;
  font-family: Roboto;
  padding: 25px 25px 30px 25px;
}

.Type0_Note img {
  display: block;
  margin: auto;
}

.Type0_Note p {
  width: 70%;
  margin: 20px auto;
  opacity: 0.5;
  font-size: 16px;
  text-align: center;
  color: #24272c;
}

.Type0_Link {
  position: relative;
  margin: auto auto auto auto;
  width: 85%;
  border-radius: 6px;
  padding: 15px 25px 0px 25px;
}

.Type0_Link p {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.25;
  text-align: center;
  color: #000000;
}

.Type0_Link img {
  width: 220px;
  display: block;
  margin: 30px auto;
}

.Type0_Link .Type0_Button {
  display: flex;
  justify-content: center;
  margin: auto;
}

.Done_Linking {
  font-weight: bold;
  margin-top: 30px;
  font-family: Roboto;
  text-align: center;
  color: #000;
}

.Done_Linking span {
  font-weight: normal;
  text-decoration: underline;
  color: #12b0ee;
}
